import React, { useState } from "react";
import { FaHome, FaQrcode, FaTicketAlt, FaUser } from "react-icons/fa";

function BottomNavbar() {
  const [activeTab, setActiveTab] = useState("home");

  return (
    <div className="fixed bottom-0 left-0 right-0 bg-white shadow-md border-t border-gray-200 md:hidden">
      <div className="flex justify-around py-2">
        <button
          onClick={() => setActiveTab("home")}
          className={`flex flex-col items-center ${
            activeTab === "home" ? "text-blue-500" : "text-gray-500"
          }`}
        >
          <FaHome className="text-2xl" />
          <span className="text-xs">Home</span>
        </button>

        <button
          onClick={() => setActiveTab("wallet")}
          className={`flex flex-col items-center ${
            activeTab === "wallet" ? "text-blue-500" : "text-gray-500"
          }`}
        >
          <FaQrcode className="text-2xl" />
          <span className="text-xs">Wallet</span>
        </button>

        <button
          onClick={() => setActiveTab("coupons")}
          className={`flex flex-col items-center ${
            activeTab === "coupons" ? "text-blue-500" : "text-gray-500"
          }`}
        >
          <FaTicketAlt className="text-2xl" />
          <span className="text-xs">Coupons</span>
        </button>

        <button
          onClick={() => setActiveTab("account")}
          className={`flex flex-col items-center ${
            activeTab === "account" ? "text-blue-500" : "text-gray-500"
          }`}
        >
          <FaUser className="text-2xl" />
          <span className="text-xs">Account</span>
        </button>
      </div>
    </div>
  );
}

export default BottomNavbar;
